import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useEffect, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useAdmissionFormPrintCase } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionSetting = ({ masterData, aid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    admission_form_print_case: "Standard",
  });

  const [admissionFormPrintCase] = useAdmissionFormPrintCase();

  useEffect(() => {
    if (masterData?.admission_form_print_case) {
      setState((prev) => ({
        ...prev,
        admission_form_print_case: masterData?.admission_form_print_case,
      }));
    }
  }, [masterData?.admission_form_print_case]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onSave = () => {
    if (aid) {
      setDisabled((pre) => !pre);
      admissionFormPrintCase({
        aid: aid,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  //   console.info("masterData", masterData);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("setting")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <CustomSelect
          selectLabel={t("admission_form_print_type")}
          selectedValue={t("admission_form_print_type_placeholder")}
          defalutValue={state.admission_form_print_case}
          onClick={(val) => onSelectDropdown(val, "admission_form_print_case")}
          options={["Standard", "Title Case", "Upper Case"]}
          customStyleContainer={{
            width: "49%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "1",
          }}
        />
        <CreateButton label="save" onAction={onSave} />
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default AdmissionSetting;
